@mixin hover {
  @media (any-hover: hover) {
    &:hover {
      @content;
    }
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}


.press {
  padding-top: 87px;
  padding-bottom: 132px;
  @include small-tablet {
    padding: 60px 0;
  }
  &__top {
    margin-bottom: 33px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include small-tablet {
      flex-wrap: wrap;
      gap: 20px;
    }
  }

  &__link {
    color: #00413E;
    text-align: center;
    font-size: var(--main-size);
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.02px;
    border: 1px solid rgba(53, 83, 98, 0.20);
  }

  &__title {
    color: var(--dark-color);
    font-family: var(--second-family);
    font-size: 27px;
    font-style: normal;
    font-weight: 400;
    line-height: 38px;
    letter-spacing: 1.2px;
  }

  &__list {
    row-gap: 52px;
    column-gap: 31px;
  }

  &__item {
    grid-column: 4 span;
    padding-right: 32px;
  }
}

.press-card {
  position: relative;
  border-top: 1px solid rgba(53, 83, 98, 0.30);

  &__top {
    padding-top: 20px;
    display: flex;
    column-gap: 16px;
    align-items: center;
    margin-bottom: 10px;
  }

  &__author {
      display: block;
      margin: -18px 0;
      padding: 18px 0;
      padding-left: 32px;
      color: rgba(53, 83, 98, 0.60);
      font-size: 11px;
      font-style: normal;
      font-weight: 400;
      line-height: 16px;
      letter-spacing: 0.5px;
      text-transform: uppercase;
      background-size: 24px 24px;
      background-repeat: no-repeat;
      background-position: left top 15px;
      transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;

    &--press-1 {
      background-image: url(../img/press/press-1.png);
    }

    &--press-2 {
      background-image: url(../img/press/press-2.png);
    }

    &--press-3 {
      background-image: url(../img/press/press-3.png);
    }

    &--press-4 {
      background-image: url(../img/press/press-4.png);
    }

    &--press-5 {
      background-image: url(../img/press/press-5.png);
    }

    &--press-6 {
      background-image: url(../img/press/press-6.png);
    }
  }

  &__date {
    color: rgba(53, 83, 98, 0.60);
    font-size: 11px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
  }

  &__title {
    margin: 0;
    transition: color 0.3s ease-in-out;
  }


  &__link {
    color: #00413E;
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: 24.65px;
    transition: 0.3s ease-in-out;
    &:focus {
      outline: none;
    }

    @include hover {
        color: var(--accent-color);
    }

    &:active {
        color: var(--active-color);
    }

    &::after {
      content: '';
      position: absolute;
      inset: 0;
    }
  }
  .press-card__link.focus-visible {
    &::after {
      outline: 1px solid var(--active-color);
    }
  }
}
